import React from "react";
import Card from "./Card";

const CardList = ({ artists }) => {
  return (
    <div>
      {artists.map((user, i) => {
        return (
          <Card
            key={i}
            id={artists[i].id}
            name={artists[i].name}
            email={artists[i].email}
            img={artists[i].img}
            sc={artists[i].sc}
          />
        );
      })}
    </div>
  );
};

export default CardList;
